@import '../../assets/sass/variables';

.card {

    background-color: $colour--background;


    border:2.5px solid black;
    border-radius: 10px;

    width: 30vw;

    display: grid;

    justify-content: center;
    align-items: center;
    justify-items: center;

    grid-template-areas:    "name       name        name    "
                            "title      title       title   "
                            "subtitle   subtitle    subtitle"
                            "add        value       sub     ";

    .card__subtitle {
        grid-area: subtitle;
    }
    .card__name {
        grid-area: name;
    }
    .card__title {
        grid-area: title;
    }
    .button-wrapper__add {
        grid-area: add;
    }
    .card__value {
        grid-area: value;
    }
    .button-wrapper__sub {
        grid-area: sub;
    }
}